}

/* Flip Cards CSS */

.card-container {
  display: grid;
  perspective: 700px;

  &:hover .card-flip {
    transform: rotateY(180deg);
  }
}

.card-flip {
  display: grid;
  grid-template: 1fr / 1fr;
  grid-template-areas: "frontAndBack";
  transform-style: preserve-3d;
  transition: all 0.7s ease;

  div {
    backface-visibility: hidden;
    transform-style: preserve-3d;
  }
}

.card-container:hover .card-flip {
  transform: rotateY(180deg);
}

.card-color {
  background: linear-gradient(to right, #248ee6, #789c51);
  font-weight: bolder;
}

.card-color1 {
  background: linear-gradient(to right, #777d83, #ecec39);
  font-weight: bolder;
}

.card-flip {
  display: grid;
  grid-template: 1fr / 1fr;
  grid-template-areas: "frontAndBack";
  transform-style: preserve-3d;
  transition: all 0.7s ease;

  div {
    backface-visibility: hidden;
    transform-style: preserve-3d;
  }
}

.front {
  grid-area: frontAndBack;
  padding: 0px !important;
  margin: 0px !important;
  height: 560px;
}

.text-justify {
  padding: 2rem;
  font-size: 1.7rem;
}

.back {
  grid-area: frontAndBack;
  transform: rotateY(-180deg);
  padding: 0px !important;
  margin: 0px !important;
  height: 560px;
}

.card-container:hover .card-flip {
  transform: rotateY(180deg);
}

.card-text {
  margin-left: 6rem;
  text-align: left;
  font-size: 1.6rem;
}

.card-text1 {
  margin-left: 0px;
  text-align: left;
  font-size: 1.3rem;
}

hr.divider1 {
  border-color: #f4623a;
}

.column {
  display: block;
  margin-left: 5rem;
  font-size: 1.4rem;
}

.blink-bg {
  color: #000;
  font-weight: bold;
  font-size: 40px;
  animation: blinkingText 5s infinite;
  margin-left: 8%;
  
  &:hover {
    text-decoration: none;
  }
}

@keyframes blinkingText {
  0% {
    color: #f8fafc;
  }
  25% {
    color: #ef0a1a;
  }

  50% {
    color: #7808ef;
  }

  75% {
    color: #3aeb0e;
  }

  100% {
    color: #240eeb;
  }
}

.img:hover {
  animation: shake 0.5s;
  animation-iteration-count: infinite;
}
//photo shake on hover over
@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg);
  }

  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }

  20% {
    transform: translate(-3px, 0px) rotate(1deg);
  }

  30% {
    transform: translate(3px, 2px) rotate(0deg);
  }

  40% {
    transform: translate(1px, -1px) rotate(1deg);
  }

  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }

  60% {
    transform: translate(-3px, 1px) rotate(0deg);
  }

  70% {
    transform: translate(3px, 1px) rotate(-1deg);
  }

  80% {
    transform: translate(-1px, -1px) rotate(1deg);
  }

  90% {
    transform: translate(1px, 2px) rotate(0deg);
  }

  100% {
    transform: translate(1px, -2px) rotate(-1deg);
  }
}
